import React from 'react';
import s from './Dialogues.module.css'
import Dialogue from "./Dialogue/Dialogue";
import Messages from "./Messages/Messages";
import { DialoguesType } from "./DialoguesContainer";
import { AppStateType } from "store/redux-store";
import { connect } from "react-redux";

const Dialogues: React.FC<DialoguesType> = (
    {
        dialoguesData,
        messagesData,
        addMessage,
        profile
    }
) => {
    return (
        <div className={s.containerDialoguesMessages}>
            <Dialogue dialoguesData={dialoguesData}
                      messagesData={messagesData}
            />
            <Messages messagesData={messagesData}
                      addMessage={addMessage}
                      profile={profile}
            />
        </div>
    );
};

// export default Dialogues;



const mapStateToProps = (state: AppStateType) => {

    return {
        // dialoguesData: state.dialoguesPage.dialoguesData,
        // messagesData: state.dialoguesPage.messagesData,
        profile: state.profilePage.profile,
    }
}

export default connect(mapStateToProps)(Dialogues)
